import { Component } from '@angular/core';
import { Observable } from 'rxjs';
import { OneConfiguratorNgrxModule, Sims, SimsFacade } from '@sales/one-configurator/data-access';
import { AsyncPipe, NgIf } from '@angular/common';

@Component({
  selector: 'tg-sales-one-configurator-feat-sims',
  standalone: true,
  templateUrl: './sales-one-configurator-feat-sims.component.html',
  imports: [OneConfiguratorNgrxModule, AsyncPipe, NgIf],
})
export class SalesOneConfiguratorFeatSimsComponent {
  readonly sims$: Observable<Sims> = this.simsFacade.sims$;

  constructor(private readonly simsFacade: SimsFacade) {}

  addSim(): void {
    this.simsFacade.addSim();
  }

  removeSim(): void {
    this.simsFacade.removeSim();
  }
}
